@if index($convertable-units, unit($value)) and\r\n\t\tindex($convertable-units, $unit)\r\n\t{\r\n\t\t@return $value /\r\n\t\t\tnth($conversion-factors, index($convertable-units, unit($value))) *\r\n\t\t\tnth($conversion-factors, index($convertable-units, $unit));\r\n\t}\r\n\r\n\t@warn \"Cannot convert `#{unit($value)}` to `#{$unit}`.\";\r\n}\r\n\r\n/// Test if `$value` is an angle\r\n/// @param {*} $value - Value to test\r\n/// @return {Bool}\r\n@function is-direction($value) {\r\n\t$is-direction: index(\r\n\t\t(\r\n\t\t\tto top,\r\n\t\t\tto top right,\r\n\t\t\tto right top,\r\n\t\t\tto right,\r\n\t\t\tto bottom right,\r\n\t\t\tto right bottom,\r\n\t\t\tto bottom,\r\n\t\t\tto bottom left,\r\n\t\t\tto left bottom,\r\n\t\t\tto left,\r\n\t\t\tto left top,\r\n\t\t\tto top left\r\n\t\t),\r\n\t\t$value\r\n\t);\r\n\t$is-angle: type-of($value) == \"number\" and\r\n\t\tindex(\"deg\" \"grad\" \"turn\" \"rad\", unit($value));\r\n\r\n\t@return $is-direction or $is-angle;\r\n}\r\n\r\n/// Convert a direction to legacy syntax\r\n/// @param {Keyword | Angle} $value - Value to convert\r\n/// @require {function} is-direction\r\n/// @require {function} convert-angle\r\n@function legacy-direction($value) {\r\n\t@if is-direction($value) == false {\r\n\t\t@warn \"Cannot convert `#{$value}` to legacy syntax because it doesn't seem to be an angle or a direction\";\r\n\t}\r\n\r\n\t$conversion-map: (\r\n\t\tto top: bottom,\r\n\t\tto top right: bottom left,\r\n\t\tto right top: left bottom,\r\n\t\tto right: left,\r\n\t\tto bottom right: top left,\r\n\t\tto right bottom: left top,\r\n\t\tto bottom: top,\r\n\t\tto bottom left: top right,\r\n\t\tto left bottom: right top,\r\n\t\tto left: right,\r\n\t\tto left top: right bottom,\r\n\t\tto top left: bottom right\r\n\t);\r\n\r\n\t@if map-has-key($conversion-map, $value) {\r\n\t\t@return map-get($conversion-map, $value);\r\n\t}\r\n\r\n\t@return 90deg - convert-angle($value, \"deg\");\r\n}\r\n","/** @format */\r\n/*\r\n\tProject variables\r\n*/\r\n$fonts: (\r\n\tmain: (\r\n\t\t\"Nunito\",\r\n\t\tsans-serif,\r\n\t),\r\n\tsecond: (\r\n\t\t\"Montserrat\",\r\n\t\tsans-serif,\r\n\t),\r\n\tawesome: \"'Font Awesome 5 Free'\",\r\n\tgeorgia: (\r\n\t\t\"'Georgia Italic'\",\r\n\t\t\"'Times New Roman'\",\r\n\t\tserif,\r\n\t),\r\n);\r\n$colors: (\r\n\tmain: #fcdd8c,\r\n\tmain_alt: #cb851f,\r\n\tsecond: #fff,\r\n\twhite: #fff,\r\n\tthird: #5c2976,\r\n\tthird_alt: #000,\r\n\tblack: #000,\r\n\tfourth: #093d5e,\r\n\tredox: #dd3333,\r\n\tblued: #14aecf,\r\n\tsoftGreen: #33a09a,\r\n\tdoraito: #c5b475,\r\n\tnewGreen: #9ecb3d,\r\n\tyell: #faa41b,\r\n\tpurpled: #aa2173,\r\n\tsea: #1d3464,\r\n\tdark: #26272b,\r\n\tdark_n1: #222327,\r\n\tblue_dark: #2c2d32,\r\n\tnarrojo: #eb3d2d,\r\n\tverde: #38d52f,\r\n\tamarillo: #ffc107,\r\n\toffline: #4b4b4e,\r\n\tpeterRiver: #3498db,\r\n\tred: #e74c3c,\r\n\tdiscord: #7289da,\r\n\tko-fi: #ff5f5f,\r\n\tgreentext: #66cc7a,\r\n);\r\n\r\n//queries\r\n$holder-break: \"screen and (max-width: 1080px)\";\r\n$mobile: \"screen and (max-width: 720px)\";\r\n// verticalFix: sumar/restar 55px al height si se agrega/quita otro icono\r\n// $query_verticalFix: 'all and (max-height: 420px) and (min-width: 645px)';\r\n// $query_detachSidebar: 'all and (max-width: 1164px) and (min-width: 645px)';\r\n// $query_relocateSecondary: 'all and (max-width: 944px)';\r\n// $query_mobile:'all and (max-width: 644px)';\r\n\r\n//sizes\r\n$min_column_width: 560px;\r\n$column_width: 60%;\r\n// $b_doted:unquote('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAGklEQVQYV2NkQAOMUP5/BgYGMBsmAFeHIQAAK+oBBROgPJEAAAAASUVORK5CYII=');\r\n// $w_doted:unquote('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAHUlEQVQYV2NkQAOMIP7/////MzIygtlgAhlgCAAAst4EBcZjUBYAAAAASUVORK5CYII=');\r\n// $n_doted:unquote('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAHUlEQVQYV2NkQAOMIP5rW93/oocvg9lgAhlgCAAArD4EBb456PcAAAAASUVORK5CYII=');\r\n$d_doted: unquote(\r\n\t\"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAHUlEQVQYV2NkQAOMIL6auvb/WzevgtlgAhlgCAAAqMoEBbLo91sAAAAASUVORK5CYII=\"\r\n);\r\n$fontsize: 12; //minimal font size in px\r\n","/** @format */\r\n/*\r\n\tA personal collection of usefull mixins\r\n\tI created the flexbox one and consider it the most useful\r\n\tSome of them are not that necesary cuz the were mean for prefixing, but autoprefixer solves that already\r\n*/\r\n//tamaño de la fuente\r\n@mixin font-size($v: 0) {\r\n\t@if $v > 0 {\r\n\t\t$n_v: $fontsize + $v;\r\n\t\tfont-size: unquote($n_v + \"px\");\r\n\t} @else {\r\n\t\tfont-size: unquote($fontsize + \"px\");\r\n\t}\r\n}\r\n// este mixin es solo valido para display:flex\r\n@mixin d_flex() {\r\n\tdisplay: -webkit-box;\r\n\tdisplay: -ms-flexbox;\r\n\tdisplay: flex;\r\n}\r\n@mixin flex-flow($type, $mode) {\r\n\t-webkit-box-orient: if($type == row, horizontal, vertical);\r\n\t-webkit-box-direction: if(str-index($type, reverse), reverse, normal);\r\n\t-ms-flex-flow: $type $mode;\r\n\tflex-flow: $type $mode;\r\n}\r\n@mixin justify-content($v) {\r\n\t$_v: str-replace($v, \"flex-\", \"\");\r\n\t$n_v: map-get(\r\n\t\t(\r\n\t\t\tspace-between: justify,\r\n\t\t\tspace-around: distribute,\r\n\t\t\tcenter: center,\r\n\t\t\tstart: start,\r\n\t\t\tend: end,\r\n\t\t),\r\n\t\t$_v\r\n\t);\r\n\t@if $n_v != distribute {\r\n\t\t-webkit-box-pack: $n_v;\r\n\t}\r\n\t-ms-flex-pack: $n_v;\r\n\tjustify-content: $v;\r\n}\r\n@mixin align-items($v) {\r\n\t$_v: str-replace($v, \"flex-\", \"\");\r\n\t-webkit-box-align: $_v;\r\n\t-ms-flex-align: $_v;\r\n\talign-items: $v;\r\n}\r\n// flexbox en una sola linea\r\n// ej: row, nowrap, space-around, strecth\r\n@mixin flexbox($t, $m, $j, $a, $d: flex) {\r\n\t@if $d != flex {\r\n\t\tdisplay: $d;\r\n\t} @else {\r\n\t\t@include d_flex();\r\n\t}\r\n\t@include flex-flow($t, $m);\r\n\t@include justify-content($j);\r\n\t@include align-items($a);\r\n}\r\n\r\n@mixin flex($v) {\r\n\t-webkit-box-flex: nth($v, 1); //toma el primer valor numerico de $v\r\n\t-ms-flex: $v;\r\n\tflex: $v;\r\n}\r\n\r\n// inicia COLUMNS\r\n@mixin columns($v) {\r\n\t-webkit-columns: $v;\r\n\t-moz-columns: $v;\r\n\tcolumns: $v;\r\n}\r\n@mixin column-width($v) {\r\n\t-webkit-column-width: $v;\r\n\t-moz-column-width: $v;\r\n\tcolumn-width: $v;\r\n}\r\n@mixin column-count($v) {\r\n\t-webkit-column-count: $v;\r\n\t-moz-column-count: $v;\r\n\tcolumn-count: $v;\r\n}\r\n@mixin column-gap($v) {\r\n\t-webkit-column-gap: $v;\r\n\t-moz-column-gap: $v;\r\n\tcolumn-gap: $v;\r\n}\r\n// termina COLUMNS\r\n\r\n/// Mixin printing a linear-gradient\r\n/// as well as a plain color fallback\r\n/// and the `-webkit-` prefixed declaration\r\n/// @access public\r\n/// @param {String | List | Angle} $direction - Linear gradient direction\r\n/// @param {Arglist} $color-stops - List of color-stops composing the gradient\r\n@mixin linear-gradient($direction, $color-stops...) {\r\n\t@if is-direction($direction) == false {\r\n\t\t$color-stops: ($direction, $color-stops);\r\n\t\t$direction: 180deg;\r\n\t}\r\n\r\n\tbackground-image: nth(nth($color-stops, 1), 1);\r\n\tbackground-image: -webkit-linear-gradient(\r\n\t\tlegacy-direction($direction),\r\n\t\t$color-stops\r\n\t);\r\n\tbackground-image: linear-gradient($direction, $color-stops);\r\n}\r\n\r\n// Flexbox Mixins\r\n// http://philipwalton.github.io/solved-by-flexbox/\r\n// https://github.com/philipwalton/solved-by-flexbox\r\n//\r\n// Copyright (c) 2013 Brian Franco\r\n\r\n@mixin flex-direction($value: row) {\r\n\t@if $value == row-reverse {\r\n\t\t-webkit-box-direction: reverse;\r\n\t\t-webkit-box-orient: horizontal;\r\n\t} @else if $value == column {\r\n\t\t-webkit-box-direction: normal;\r\n\t\t-webkit-box-orient: vertical;\r\n\t} @else if $value == column-reverse {\r\n\t\t-webkit-box-direction: reverse;\r\n\t\t-webkit-box-orient: vertical;\r\n\t} @else {\r\n\t\t-webkit-box-direction: normal;\r\n\t\t-webkit-box-orient: horizontal;\r\n\t}\r\n\t-webkit-flex-direction: $value;\r\n\t-moz-flex-direction: $value;\r\n\t-ms-flex-direction: $value;\r\n\tflex-direction: $value;\r\n}\r\n@mixin flex-wrap($value: nowrap) {\r\n\t// No Webkit Box fallback.\r\n\t-webkit-flex-wrap: $value;\r\n\t-moz-flex-wrap: $value;\r\n\t@if $value == nowrap {\r\n\t\t-ms-flex-wrap: none;\r\n\t} @else {\r\n\t\t-ms-flex-wrap: $value;\r\n\t}\r\n\tflex-wrap: $value;\r\n}\r\n@mixin order($int: 0) {\r\n\t-webkit-box-ordinal-group: $int + 1;\r\n\t-webkit-order: $int;\r\n\t-moz-order: $int;\r\n\t-ms-flex-order: $int;\r\n\torder: $int;\r\n}\r\n\r\n/*\r\n\tMixins\r\n\tAuthor - Andy Crone | @andycrone_\r\n\t+ Popular mixins including vendor prefixes for cross-browser support\r\n*/\r\n\r\n// === Standard layout & styling\r\n\r\n@mixin box-sizing($v) {\r\n\t-webkit-box-sizing: $v;\r\n\t-moz-box-sizing: $v;\r\n\tbox-sizing: $v;\r\n}\r\n@mixin box-shadow($v) {\r\n\t-webkit-box-shadow: $v;\r\n\t-moz-box-shadow: $v;\r\n\tbox-shadow: $v;\r\n}\r\n@mixin border-radius($v) {\r\n\t-moz-border-radius: $v;\r\n\t-webkit-border-radius: $v;\r\n\tborder-radius: $v;\r\n}\r\n@mixin background-size($v) {\r\n\t-o-background-size: $v;\r\n\t-webkit-background-size: $v;\r\n\t-moz-background-size: $v;\r\n\tbackground-size: $v;\r\n}\r\n// @mixin linear-gradient($angle, $gradient1, $gradient2) {\r\n// \t// background-color: $gradient2; /* Fallback Color */\r\n// \tbackground-image: -webkit-gradient(linear, $angle, from($gradient1), to($gradient2)); /* Saf4+, Chrome */\r\n// \tbackground-image: -webkit-linear-gradient($angle, $gradient1, $gradient2); /* Chrome 10+, Saf5.1+, iOS 5+ */\r\n// \tbackground-image:    -moz-linear-gradient($angle, $gradient1, $gradient2); /* FF3.6 */\r\n// \tbackground-image:     -ms-linear-gradient($angle, $gradient1, $gradient2); /* IE10 */\r\n// \tbackground-image:      -o-linear-gradient($angle, $gradient1, $gradient2); /* Opera 11.10+ */\r\n// \tbackground-image:         linear-gradient($angle, $gradient1, $gradient2);\r\n// \tfilter: progid:DXImageTransform.Microsoft.gradient(GradientType=$angle,StartColorStr='#{$gradient1}',